*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

img {
    max-width: 100%; 
    height: auto; 
    display: block; 
    object-fit: cover;
}

li{
    list-style: none;
}

.App{
    width: 100vw;
    min-height: 100vh;
    display: grid;
    grid-template-columns: 5fr 2fr;
    overflow: hidden;

}

.left-side{
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 50px;
    background-color: rgba(0, 0, 0, 0.397);
}

.small-container{
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 15px;
    font-size: 3rem;
}
.second-container{
    text-align: center;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    font-size: 2.5rem;
}

#main{
    font-size: 8rem;
}

.right-side{
    background-color: rgba(0, 0, 0, 0.829);
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.right-side input{
    margin-top: 20%;
    width: 80%;
    text-indent: 10%;
    font-size: 3rem;
    background-color: transparent;
    border: none;
    outline: none;
    color: white;
    border-bottom: 3px solid white;
}

.right-side ul{
    width: 80%;
    margin-top: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 50px;
}

li{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    gap: 20px;
    font-size: 1.7rem;
}

i{
    background-color: black;
    border: 1px solid white;
    width: 40px;
    height: 40px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center ;
    text-align: center;
}

.appear {
    opacity: 0; /* Start the text with 0 opacity */
    animation: fade-in 1s cubic-bezier(0.165, 0.84, 0.44, 1) forwards; /* Apply the "fade-in" animation */
  }
  
  @keyframes fade-in {
    0% {
      opacity: 0;
      transform: translateY(20px); /* Move the text down a bit */
    }
    100% {
      opacity: 1;
      transform: translateY(0); /* Move the text back to its original position */
    }
  }

  .appearscnd {
    opacity: 0; /* Start the text with 0 opacity */
    animation: fade-in 3s cubic-bezier(0.165, 0.84, 0.44, 1) forwards; /* Apply the "fade-in" animation */
  }
  
  @keyframes fade-in {
    0% {
      opacity: 0;
      transform: translateY(20px); /* Move the text down a bit */
    }
    100% {
      opacity: 1;
      transform: translateY(0); /* Move the text back to its original position */
    }
  }


@media all and (max-width: 1300px) {
    .App{
        grid-template-columns: 5fr 3fr;
    }
}

@media all and (max-width: 1000px) {
    .App{
        grid-template-columns: 5fr 4fr;
    }
    .small-container{
        font-size: 2.5rem;
    }
    .second-container{
        flex-direction: column;
        font-size: 2.5rem;
    }
    
    #main{
        font-size: 5rem;
    }
    
    .right-side input{
        font-size: 2rem;
    }
    
    li{
        font-size: 1.3rem;
    }
}

@media all and (max-width: 700px) {
    *{
        text-align: center;
    }
    .App{
        grid-template-columns: 1fr;
    }

    .right-side input{
        width: 90%;
        position: absolute;
        top: 5%;
        margin-top: 0;
        font-size: 3rem;
        text-indent: 0px;
        text-align: center;
        background: transparent;
    }

    .second-container{
        gap: 5px;
        margin-bottom: 5%;
    }

    .left-side{
        background: transparent;
        margin-top: 30%;
    }

    .right-side{
        background: transparent;
    }
    
    li{
        font-size: 1.8rem;
    }
}

@media all and (max-width: 400px) {
    .right-side input{
        width: 90%;
        position: absolute;
        top: 5%;
        margin-top: 0;
        font-size: 2.3rem;
        text-indent: 0px;
        text-align: center;
    }

    .left-side{
        margin-top: 50%;
    }


    .second-container{
        margin-top: 5%;
        flex-direction: column;
        gap: 5px;
        margin-bottom: 5%;
    }
    
    #main{
        font-size: 4rem;
    }

    li{
        font-size: 1.4rem;
        font-weight: bold;
        padding-bottom: 15px;
    }
}

@media all and (max-width: 320px) {
    .right-side input{
        width: 90%;
        position: absolute;
        top: 5%;
        margin-top: 0;
        font-size: 2.5rem;
        text-indent: 0px;
        text-align: center;
    }

    .left-side{
        margin-top: 50%;
    }

    .small-container, .second-container{
        font-size: 2rem;
    }

    li{
        font-size: 1.1rem;
        font-weight: bold;
    }
}